<template>
  <div id="myDay">
    <nav-bar>
      <input
        slot="center"
        class="search"
        type="text"
        placeholder="æ·»åŠ todo"
        v-model="inputValue"
        @keyup.enter="addItem"
      />
    </nav-bar>
    <task-bar class="todo">
      <span
        slot="tagging"
        @mouseover="showDetailM('todo')"
        @mouseout="hideDetailM('todo')"
        @click="clearItem(false)"
      >
        ToDo({{ todoCount }})
        <show-detail
          text="ç‚¹æˆ‘æ¸…ç©º"
          :display="displayTodo"
          top="0.2rem"
          left="1rem"
        ></show-detail>
      </span>
      <span v-if="!todoCount">ä½ è¿˜æ·»åŠ ä»»ä½•å¾…åŠ</span>
      <transition-group name="ani">
        <list-item
          v-for="(item, index) in mydayList"
          :key="item.tid"
          v-show="!item.done"
          :item="item.title"
          :done="item.done"
          @deleteItem="deleteItem(index)"
          @isDone="isDone(index)"
        ></list-item>
      </transition-group>
    </task-bar>
    <task-bar class="done">
      <!-- <task-bar :tagging="'Done' + '(' + doneCount + ')'" class="done" > -->
      <span
        slot="tagging"
        @mouseover="showDetailM('done')"
        @mouseout="hideDetailM('done')"
        @click="clearItem(true)"
      >
        Done({{ doneCount }})
        <show-detail
          text="ç‚¹æˆ‘æ¸…ç©º"
          :display="displayDone"
          top="0.2rem"
          left="1rem"
        ></show-detail>
      </span>
      <span v-if="!doneCount">ä½ è¿˜æœªå®Œæˆä»»ä½•å¾…åŠ</span>
      <transition-group name="ani">
        <list-item
          v-for="(item, index) in mydayList"
          :key="item.tid"
          v-show="item.done"
          :item="item.title"
          :done="item.done"
          @deleteItem="deleteItem(index)"
          @isDone="isDone(index)"
        ></list-item>
      </transition-group>
    </task-bar>
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar.vue";
import TaskBar from "components/content/taskbar/TaskBar.vue";
import ListItem from "components/content/listItem/ListItem.vue";
import ShowDetail from "components/content/showDetail/ShowDetail.vue";

import {
  getData,
  saveData,
  deleteData,
  getDateStr,
} from "assets/js/todoList.js";

export default {
  name: "MyDay",
  components: {
    NavBar,
    TaskBar,
    ListItem,
    ShowDetail,
  },
  data() {
    return {
      mydayList: [
        // {title: 'ğŸåƒè‹¹æœ', done: false, createDate: "2021-03-26"},
        // {title: 'ğŸ“åƒè‰è“', done: false, createDate: "2021-03-26"},
        // {title: 'ğŸŒåƒé¦™è•‰', done: false, createDate: "2021-03-26"},
        // {title: 'ğŸˆåƒæ©˜å­', done: false, createDate: "2021-03-26"},
        // {title: 'ğŸ‰åƒè¥¿ç“œ', done: false, createDate: "2021-03-26"},
      ],
      inputValue: "",
      dateNow: "",
      displayTodo: "none",
      displayDone: "none",
    };
  },
  computed: {
    //æœªå®Œæˆæ•°é‡
    todoCount() {
      return this.mydayList.filter((item) => {
        return !item.done;
      }).length;
    },

    //å·²å®Œæˆæ•°é‡
    doneCount() {
      return this.mydayList.filter((item) => {
        return item.done;
      }).length;
    },
  },
  // é¡µé¢åŠ è½½å³æ¸²æŸ“æ‰€æœ‰æ•°æ®
  created() {
    let allList = getData("myday");

    //å¾—åˆ°å½“å‰æ—¶é—´
    this.dateNow = getDateStr();
    let data = getData("myNoTodo");

    // ä¸å¯è¡Œ
    // allList.forEach((item, index) => {
    //   if(item.createDate !== this.dateNow) {
    //     data.push(item)
    //     // allList.splice(index, 1) //ä¸å¯è¡Œï¼Œindex åœ¨allListè¢«åˆ çš„è¿‡ç¨‹ä¸­æ”¹å˜äº†
    //     console.log(index)
    //     this.mydayList.splice(index, 1)
    //   }
    // })
    // allList = this.mydayList

    // åˆ†ç±»æ•°æ®
    allList = allList.filter((item, index) => {
      if (item.createDate !== this.dateNow && !item.done) {
        data.push(item);
      } else {
        return item;
      }
    });

    this.mydayList = allList;

    saveData(allList, "myday");
    // this.mydayList = allList
    saveData(data, "myNoTodo");
  },

  //ç›‘å¬æ•°æ®å˜åŒ–
  watch: {
    mydayList: {
      // è¯¥å›è°ƒä¼šåœ¨ä»»ä½•è¢«ä¾¦å¬çš„å¯¹è±¡çš„ mydayList æ”¹å˜æ—¶è¢«è°ƒç”¨ï¼Œä¸è®ºå…¶è¢«åµŒå¥—å¤šæ·±
      handler() {
        saveData(this.mydayList, "myday");
        getData();
      },
      deep: true,
    },
  },

  methods: {
    // æ·»åŠ 
    addItem(e) {
      let data = e.target.value;
      if (data === "") {
        return alert("è¯·è¾“å…¥å¾…åŠ");
      }
      const createDate = getDateStr();
      const tid = new Date().valueOf();
      this.mydayList.push({ title: data, done: false, createDate, tid });
      this.inputValue = "";
    },
    //åˆ é™¤
    deleteItem(index) {
      this.mydayList.splice(index, 1);
    },
    // æ¸…é™¤å¾…åŠ
    clearItem(done) {
      let sure = confirm("ç¡®å®šæ¸…ç©ºè¯¥ç±»å¾…åŠï¼Ÿ");
      if (sure) {
        this.mydayList = this.mydayList.filter((item) => {
          return item.done !== done;
        });
      }
    },
    //å®Œæˆ
    isDone(index) {
      this.mydayList[index].done = !this.mydayList[index].done;
    },
    // å±•ç¤ºæç¤ºä¿¡æ¯
    showDetailM(type) {
      switch (type) {
        case "todo":
          this.displayTodo = "block";
          break;
        case "done":
          this.displayDone = "block";
          break;
        default:
          this.displayTodo = "block";
          break;
      }
    },
    // éšè—æç¤ºä¿¡æ¯
    hideDetailM(type) {
      switch (type) {
        case "todo":
          this.displayTodo = "none";
          break;
        case "done":
          this.displayDone = "none";
          break;
        default:
          this.displayTodo = "none";
          break;
      }
    },
  },
};
</script>

<style scoped lang="less">
#myDay {
  .todo {
    width: 3.6rem;
    position: absolute;
    right: 5.28rem;
  }
  .done {
    width: 3.6rem;
    position: absolute;
    right: 0.75rem;
  }
}
</style>